import React, { useState } from "react";
import { connect } from "react-redux";
import ToDoModel from "./ToDoModel";
import "./Dashbord.css";
const mapStateToProps = (state) => {
  return state.User;
};

function StudentDashboard(props) {
  let request = "";
  const { faculty_approve, faculty_comment } = props.credentials;
  const profile = () => {
    let profileStatus;
    let buttonClass;
    if (faculty_approve === 0) {
      profileStatus = "Pending";
      buttonClass = "btn btn-outline-primary btn-sm";
    } else if (faculty_approve === 1) {
      profileStatus = "Accepted";
      buttonClass = "btn btn-outline-success btn-sm";
    } else {
      profileStatus = "Rejected";
      buttonClass = "btn btn-outline-danger btn-sm";
    }
    let cardClassname = `card col-lg-2 m-2 mx-auto ${profileStatus}`;
    return (
      <div className={cardClassname} style={{ width: "80%" }}>
        <div className="card-header">
          <p style={{ margin: "auto" }}>PROFILE</p>
        </div>
        <div className="card-body">
          <p>
            <strong>{profileStatus}</strong>
          </p>
          <button
            type="button"
            className={buttonClass}
            data-toggle="modal"
            data-target="#ToDoNext"
            status={profileStatus}
            department={"Profile"}
            comment={faculty_comment}
            onClick={setModelData}
          >
            See Details
          </button>
        </div>
      </div>
    );
  };

  const [requestStatus, setRequestStatus] = useState(null);
  const [Department, setDepartment] = useState(null);
  const [comment, setComment] = useState(null);
  const setModelData = (e) => {
    setComment(e.target.getAttribute("comment"));
    setDepartment(e.target.getAttribute("department"));
    setRequestStatus(e.target.getAttribute("status"));
  };
  if (props.credentials.request) {
    var data = props.credentials.request;

    request = data.map((element, index) => {
      var dept = "";
      var status = "";
      var buttonClass = "";
      var comment = element.comment;

      if (
        element.status3 === 2 ||
        element.status2 === 2 ||
        element.status1 === 2
      ) {
        if (element.status1 === 2) dept = "Department";
        else if (element.status2 === 2) dept = "Student Section";
        else dept = "Student Section Head";
        buttonClass = "btn btn-outline-danger btn-sm";
        status = "Rejected";
      } else {
        if (element.status1 === 0) dept = "Department";
        else if (element.status2 === 0) dept = "Student Section";
        else dept = "Student Section Head";
        buttonClass = "btn btn-outline-primary btn-sm";
        status = "Pending";
      }
      if (element.status3 === 1) {
        dept = "Student Section Head";
        status = "Accepted";
        buttonClass = "btn btn-outline-success btn-sm";
      }
      let title = element.type.toUpperCase();
      let cardClassname = `card col-lg-2 m-2 mx-auto ${status}`;
      return (
        <div className={cardClassname} style={{ width: "80%" }}>
          <div className="card-header">
            <p style={{ margin: "auto" }}>{title}</p>
          </div>
          <div className="card-body">
            <p>
              <strong>{status}</strong>
            </p>
            <button
              type="button"
              className={buttonClass}
              data-toggle="modal"
              data-target="#ToDoNext"
              status={status}
              department={dept}
              comment={comment}
              onClick={setModelData}
            >
              See Details
            </button>
          </div>
        </div>
      );
    });
  }

  return (
    <div className="container mt-5">
      <div className="row text-center">
        {profile()}
        {request}
        <ToDoModel
          status={requestStatus}
          comment={comment}
          department={Department}
        />
      </div>
    </div>
  );
}

export default connect(mapStateToProps)(StudentDashboard);
