import React, { useState, useEffect } from 'react';

function ToDoModel(props) {
	const { status, department, comment } = props;
	const [ message, setMessage ] = useState('');
	const [ todo, setToDo ] = useState(null);
	useEffect(
		() => {
			if (department === "Profile") {
				setMessage(`Your application is ${status} at department`);
				if (status === "Pending") {
					setToDo("You can not request new certificate while your registration is under progress")	
				}
				else if (status === "Rejected") {
					setToDo(`Reason : ${comment}`)	
				}
				else {
					setToDo("Now You can request any certificate you want")	
				}
				
			}
			else if (status === 'Pending') {
				setMessage(`Your application is Pending at ${department}`);
				setToDo("Wait while you application is under progress")
			} else if (status === 'Accepted') {
				setMessage(`Your application is ${status} by ${department}`);
				setToDo('Please visit student section for issue your certificate');
			} else if (status === 'Rejected') {
				setMessage(`Your application is ${status} by ${department}`);
				setToDo(`Reason : ${comment}`);
			}
		},
		[ status, department ]
	);

	return (
		<div className="modal fade" id="ToDoNext" tabindex="-1" role="dialog" aria-labelledby="todonext">
			<div className="modal-dialog" role="document">
				<div className="modal-content">
					<div className="modal-header">
						<h4 className="modal-title" id="change_photo_title">
							What to Do Next?
						</h4>
						<button type="button" className="close" data-dismiss="modal" aria-label="Close">
							<span aria-hidden="true">&times;</span>
						</button>
					</div>

					<div className="modal-body">
						<h5>{message}</h5>
						{todo && <h5>{todo}</h5>}
						<div className="modal-footer">
							<button
								type="button"
								id="close-modal-photo"
								className="btn btn-danger"
								data-dismiss="modal"
							>
								Cancel
							</button>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
}

export default ToDoModel;
